.receipt-div{
  margin-left: 30%;
  margin-right: 30%;
  margin-bottom: 5%;
}
#search{
  margin-top: 5%;
  padding-right: 25%;
  padding-left: 25%;
  text-align: center;
}
#print{
  border-radius: 30px;
}

#table{
  border: 1px solid black;
  text-align: center;
  width: 100%;
}

td{
  border-left: 1px solid black;
  border-bottom: 1px solid black;
  width: 10%;
}

th{
  border-left: 1px solid black;
  border-bottom: 1px solid black;
  width: 10%;
}


@media screen and (max-width: 767px){
  /* for mobile phones */
  .receipt-div{
    margin: 10%;
    /* border-right: 1px solid black; */
  }
  img{
    width: 100%;
  }
  .receipt-img{
    width: 70%;
  }
  .hide{
    display: none;
  }
  .print{
    border-right: 1px solid black;
  }
  .receipt-link{
    text-align: left;
  }
}


@media screen and (min-width: 768px){
  .form-div{
    margin-left: 30%;
    margin-right: 30%;
  }
  .receipt-div{
    border: 1px solid black;
  }
  .receipt-img{
    width: 70%;
  }
  .receipt-link{
    text-align: left;
  }
}

@media print {
  .receipt{
    border: 1px solid black;
    text-align: left;
  }
  button{
    display: none;
  }
  .receipt-div{
    justify-content: left;
    top: 0%;
    margin-left: 0%;
    margin-top: 0%;
    padding-left: 0%;
    padding-top: 0%;
    width: 50%;
    height: 100%;
  }
  .receipt-img{
    height: 5rem;
    width: 10rem;
    padding-top: 0%;
    margin-top: 0%;
  }
  .receipt-total{
    text-align: left;
    margin-left: 0%;
    padding-left: 0%;
  }
  .receipt-link{
    display: none;
  }
}

